import { useState, useRef } from 'react';
import styled, { css } from 'styled-components';
import { IoIosArrowForward } from 'react-icons/io';

// TODO: 높이 auto 상태에서 값을 못바꾸고 토글 되는 현상 존재
export default function SidebarElement({ content, depth }) {
  // content: {title: string, subContents?: content[]}
  const [{ title, subContents }] = useState(content);
  const [view, setView] = useState(false);

  // 트랜지션동안 중복된 토글 요청에대한 무시
  const duplicateReq = useRef();

  // 부모의 높이를 0으로 해도 자식의 높이는 그대로라는 점을 이용하여 부모 높이 처리
  const heightListener = useRef();
  const heightHandler = useRef();

  const onToggle = () => {
    // ref를 잡고 바로 높이를 정하면 아직 렌더링 하기 전이기 때문에 요소를 찾을 수 없다.
    heightHandler.current.style.setProperty(
      '--height',
      `${heightListener.current.offsetHeight}px`
    );

    duplicateReq.current = setTimeout(() => setView(!view), 100);
  };

  // 높이를 auto로 설정해서 하위 컴포넌트 토글해도 마음껏 늘어날 수 있게 설정
  // 그러나 닫을때는 높이를 구체적으로 지정해서 트랜지션이 작동하게 설정
  // TODO: 이밴트 버블링으로 자식 컴포넌트의 이벤트가 마무리 되어도 부모이벤트가 실행되는 현상 존재
  const transitionEndHandler = () => {
    const height = parseInt(heightHandler.current.offsetHeight);
    if (height > 0) {
      console.log(height);
      heightHandler.current.style.setProperty('--height', 'auto');
    }

    // 최초 한번의 요청만 트랜지션되고 트랜지션이 종료되면 중복된 요청은 제거
    if (duplicateReq.current) {
      clearTimeout(duplicateReq.current);
      duplicateReq.current = null;
    }
  };

  return (
    <SidebarElementBlock>
      <Title
        onToggle={onToggle}
        hasContent={!!subContents}
        view={view}
        title={title}
      />
      <Wrapper
        ref={heightHandler}
        $view={view}
        onTransitionEnd={transitionEndHandler}
      >
        <ol ref={heightListener}>
          {subContents &&
            subContents.map((subContent, i) => (
              <SidebarElement key={i} content={subContent} depth={depth + 1} />
            ))}
        </ol>
      </Wrapper>
    </SidebarElementBlock>
  );
}

function Title({ onToggle, hasContent, view, title }) {
  return (
    <TitleBlock onClick={onToggle}>
      <TitlePrefix content={!!hasContent} view={view} />
      {title}
    </TitleBlock>
  );
}

const TitleBlock = styled.div`
  display: flex;
  justify-content: flex-start;
  align-items: center;
`;

function TitlePrefix({ content, view }) {
  if (content) {
    return (
      <TitlePrefixBlock $view={view}>
        <IoIosArrowForward />
      </TitlePrefixBlock>
    );
  } else {
    return null;
  }
}

const TitlePrefixBlock = styled.div`
  transition: transform 0.2s ease-out;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 25px;
  height: 25px;
  margin-right: 0.3rem;

  ${({ $view }) =>
    $view &&
    css`
      transform: rotate(90deg);
    `}
`;

const SidebarElementBlock = styled.li`
  padding: 1.5rem 0 0 1.2rem;
  font-size: 1.3rem;
  border: 1px solid black;
  cursor: pointer;
`;

const Wrapper = styled.div`
  max-height: 0;
  overflow: hidden;

  transition: max-height 0.4s ease-in-out;
  ${({ $view }) =>
    $view &&
    css`
      max-height: var(--height);
    `}
`;
