import styled from 'styled-components';
import { Link } from 'react-router-dom';
const NotFound = () => {
  return (
    <NotFoundBlock>
      <h2>404 Not Found</h2>
      <Link to="/">back</Link>
    </NotFoundBlock>
  );
};

const NotFoundBlock = styled.div`
  text-align: center;
  h2 {
    font-size: 2rem;
    font-weight: bold;
    padding: 1rem;
  }
`;
export default NotFound;
