import styled from 'styled-components';
import { Link } from 'react-router-dom';
import { FiMenu } from 'react-icons/fi';
import logoImg from '../assets/logo.png';

import Sidebar from './Sidebar';
import { useState } from 'react';

const Header = () => {
  const [viewSidebar, setViewSidebar] = useState(false);
  const onToggle = () => {
    setViewSidebar(!viewSidebar);
  };

  return (
    <>
      <HeaderPositioner>
        <Link to="/" style={{ textDecoration: 'none' }}>
          <Logo />
        </Link>
        <AsideButton onToggle={onToggle} />
      </HeaderPositioner>
      <Sidebar view={viewSidebar} onToggle={onToggle} right={true} />
    </>
  );
};

const HeaderPositioner = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
`;

const Logo = () => {
  return (
    <LogoBlock>
      <img src={logoImg} alt="전북대학교" className="logo-img" />
      <div className="bar">|</div>
      <LogoTextBlock>
        <h1 className="logo-text">IT정보공학과</h1>
        <h1 className="logo-text">IT지능정보공학과</h1>
      </LogoTextBlock>
    </LogoBlock>
  );
};

const LogoTextBlock = styled.div`
  line-height: 1.2rem;
`;

const LogoBlock = styled.div`
  display: flex;
  align-items: center;
  .logo-img {
    width: 116px;
  }

  .bar {
    padding: 0 0.5rem;
    color: #dee2e6;
    font-size: 1.5rem;
  }

  .logo-text {
    font-size: 1rem;
    color: #005bac;
    font-weight: 400;
  }
`;

const AsideButton = ({ onToggle }) => {
  return (
    <AsideButtonBlock onClick={onToggle}>
      <FiMenu size={20} />
    </AsideButtonBlock>
  );
};

const AsideButtonBlock = styled.div``;

export default Header;
