import styled, { css } from 'styled-components';
import { useState, useEffect } from 'react';

import SidebarContents from './SidebarContents';
import scrollLock from './scrollLock';

export default function Sidebar({ view, onToggle, right }) {
  const [localView, setLocalView] = useState(false);

  // 사이드바가 삭제되면 백그라운드 트랜지션을 보여주기위해 로컬뷰 토글
  useEffect(() => {
    if (view) {
      setLocalView(true);
    }

    // 사이드바 생성시 배경 스크롤 잠금(ios safari 대응)
    scrollLock(view);
  }, [view]);

  // 사라지는 모션은 onTransitionEnd를 이용하여 트랜지션이 종료되면 백그라운드도 사라지도록 설정
  const TransitionEndHandler = () => {
    if (!view) {
      setLocalView(false);
    }
  };

  return (
    <>
      <Background
        onClick={onToggle}
        $view={view}
        $localView={localView}
        onTransitionEnd={TransitionEndHandler}
      />
      <SidebarBlock $position={right} $view={view}>
        <SidebarContents />
      </SidebarBlock>
    </>
  );
}

const Background = styled.div`
  background-color: rgba(0, 0, 0, 0.2);
  width: 100%;
  height: var(--vh, 100vh);
  position: absolute;
  top: 0;
  z-index: -1;
  overflow: hidden;
  opacity: 0;

  transition: opacity 0.25s ease;

  ${({ $view, $localView }) => {
    if ($view && $localView) {
      return css`
        z-index: 100;
        opacity: 1;
      `;
    } else if ($localView) {
      return css`
        z-index: 100;
      `;
    }
  }}
`;

const SidebarBlock = styled.div`
  height: var(--vh, 100vh);
  background-color: #fff;
  width: 75%;
  top: 0;
  position: absolute;
  overflow-y: auto;
  z-index: 101;
  margin: 0px;
  padding: 0px;

  transform: translateX(100%);
  transition: transform ease 0.4s;

  ${({ $position }) => {
    return $position
      ? css`
          right: 0;
        `
      : css`
          left: 0;
        `;
  }}

  ${({ $view }) =>
    $view &&
    css`
      transform: translateX(0);
    `}
`;
