import { getNavContents } from './navContents';
import styled from 'styled-components';
import SidebarElement from './SidebarElement';

const contents = getNavContents();

export default function SidebarContents() {
  return (
    <SidebarContentsBlock>
      <ol>
        {contents.map((content, i) => (
          <SidebarElement key={i} content={content} depth={0} />
        ))}
      </ol>
    </SidebarContentsBlock>
  );
}

const SidebarContentsBlock = styled.div`
  padding: 1rem 0 1rem 0rem;
`;
