import styled from 'styled-components';
import subVisualImg from '../assets/sub_visual.jpg';

const SubVisual = () => {
  return <SubVisualBlock />;
};
const SubVisualBlock = styled.div`
  width: 100%;
  height: 80px;
  // background: color url repeat position
  background: url(${subVisualImg}) no-repeat center;
  background-size: cover;
`;

export default SubVisual;
